.card {
    width: 250px;
    height: 300px;
    border: 5px solid #1d2733;
    border-radius: 20px;
    box-sizing: border-box;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease;
    cursor: pointer;
}

.card:hover {
    transform: perspective(600px)  rotateY(-10deg) translateY(-10px);
}

.card__image {
    width: 250px;
    height: 250px;
}

.card__title {
    width: 225px;
    color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    line-height: 25px;
    text-align: center;
    margin: 20px auto 30px auto;
    white-space: pre-line;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .card {
        width: 300px;
    }

    .card__title {
        font-size: 25px;
        width: 270px;
    }
}

